"use client"

import React, { useState } from 'react'
import { useAuth, UserProfile } from '@/lib/auth-context' // Import auth hook and profile type

interface AuthModalProps {
  mode: 'login' | 'signup'
  onClose: () => void
  // 'onSuccess' prop is no longer needed, as the auth context handles this
}

type ProfileData = Omit<UserProfile, 'email'>

export default function AuthModal({ mode, onClose }: AuthModalProps) {
  const [currentMode, setCurrentMode] = useState(mode)
  const [step, setStep] = useState(1) // For multi-step signup
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [error, setError] = useState<string | null>(null)
  const [loading, setLoading] = useState(false)

  // State for Step 2 profile data
  const [profile, setProfile] = useState<ProfileData>({
    name: '',
    gender: '',
    age: '',
    school: '',
    studentId: '',
  })

  const { login, signupAndCreateProfile } = useAuth()

  const handleProfileChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>
  ) => {
    const { name, value } = e.target
    setProfile((prev) => ({ ...prev, [name]: value }))
  }

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault()
    setError(null)
    setLoading(true)
    try {
      await login(email, password)
      onClose() // Close modal on success
    } catch (err) {
      // Firebase ÏóêÎü¨ Î©îÏãúÏßÄÎ•º ÏÇ¨Ïö©Ïûê ÏπúÌôîÏ†ÅÏúºÎ°ú Î≥ÄÍ≤Ω
      let message = (err as Error).message
      if (message.includes('auth/invalid-credential')) {
        message = 'Ïù¥Î©îÏùº ÎòêÎäî ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§.'
      } else if (message.includes('auth/user-not-found')) {
        message = 'Ï°¥Ïû¨ÌïòÏßÄ ÏïäÎäî ÏÇ¨Ïö©ÏûêÏûÖÎãàÎã§.'
      }
      setError(message)
    } finally {
      setLoading(false)
    }
  }

  const handleSignupStep1 = (e: React.FormEvent) => {
    e.preventDefault()
    // Basic validation
    if (!email || !password) {
      setError('Ïù¥Î©îÏùºÍ≥º ÎπÑÎ∞ÄÎ≤àÌò∏Î•º Î™®Îëê ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî.')
      return
    }
    if (password.length < 6) {
      setError('ÎπÑÎ∞ÄÎ≤àÌò∏Îäî 6ÏûêÎ¶¨ Ïù¥ÏÉÅÏù¥Ïñ¥Ïïº Ìï©ÎãàÎã§.')
      return
    }
    setError(null)
    setStep(2) // Move to step 2
  }

  const handleSignupStep2 = async (e: React.FormEvent) => {
    e.preventDefault()
    // Check if all fields are filled
    const allFieldsFilled = Object.values(profile).every(field => field.trim() !== '');
    if (!allFieldsFilled) {
      setError('Î™®Îì† ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥Î•º ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî.');
      return;
    }

    setError(null)
    setLoading(true)
    try {
      // Call the new function from context
      await signupAndCreateProfile(email, password, profile)
      onClose() // Close modal on success (auto-login)
    } catch (err) {
      let message = (err as Error).message
      if (message.includes('auth/email-already-in-use')) {
        message = 'Ïù¥ÎØ∏ ÏÇ¨Ïö© Ï§ëÏù∏ Ïù¥Î©îÏùºÏûÖÎãàÎã§.'
        setStep(1); // Go back to step 1
      }
      setError(message)
    } finally {
      setLoading(false)
    }
  }

  // Î™®Îã¨ Îí∑Î∞∞Í≤Ω ÌÅ¥Î¶≠ Ïãú Îã´Í∏∞ (Î°úÎî© Ï§ë ÏïÑÎãê Îïå)
  const handleBackdropClick = () => {
    if (!loading) {
      onClose()
    }
  }

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50" onClick={handleBackdropClick}>
      <div className="bg-white rounded-lg shadow-xl p-8 w-full max-w-md" onClick={(e) => e.stopPropagation()}>
        <h2 className="text-2xl font-bold text-center mb-6">
          {currentMode === 'login'
            ? 'Î°úÍ∑∏Ïù∏'
            : 'ÌöåÏõêÍ∞ÄÏûÖ'}
        </h2>

        {error && <p className="text-red-500 text-center mb-4">{error}</p>}

        {/* --- LOGIN FORM / SIGNUP STEP 1 --- */}
        {step === 1 && (
          <form onSubmit={currentMode === 'login' ? handleLogin : handleSignupStep1}>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700 mb-1">Ïù¥Î©îÏùº</label>
              <input
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm"
                required
              />
            </div>
            <div className="mb-6">
              <label className="block text-sm font-medium text-gray-700 mb-1">ÎπÑÎ∞ÄÎ≤àÌò∏</label>
              <input
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm"
                required
                maxLength={72}
              />
            </div>
            <button
              type="submit"
              disabled={loading}
              className="w-full bg-[#9DB78C] text-white py-2 rounded-md hover:bg-[#8AA876] disabled:opacity-50"
            >
              {loading
                ? 'Ï≤òÎ¶¨ Ï§ë...'
                : currentMode === 'login'
                ? 'Î°úÍ∑∏Ïù∏'
                : 'Îã§Ïùå'}
            </button>
          </form>
        )}

        {/* --- SIGNUP STEP 2 (PROFILE) --- */}
        {currentMode === 'signup' && step === 2 && (
          <form onSubmit={handleSignupStep2}>
            <p className="text-sm text-gray-600 mb-4">ÎßàÏßÄÎßâ Îã®Í≥ÑÏûÖÎãàÎã§. ÌîÑÎ°úÌïÑÏùÑ ÏôÑÏÑ±Ìï¥ Ï£ºÏÑ∏Ïöî.</p>
            
            {/* Input fields for profile data */}
            <div className="grid grid-cols-2 gap-4">
              <div className="mb-4">
                <label className="block text-sm font-medium text-gray-700 mb-1">Ïù¥Î¶Ñ</label>
                <input type="text" name="name" value={profile.name} onChange={handleProfileChange} className="w-full px-3 py-2 border border-gray-300 rounded-md" required />
              </div>
              <div className="mb-4">
                <label className="block text-sm font-medium text-gray-700 mb-1">ÏÑ±Î≥Ñ</label>
                <select name="gender" value={profile.gender} onChange={handleProfileChange} className="w-full px-3 py-2 border border-gray-300 rounded-md" required>
                  <option value="">ÏÑ†ÌÉù</option>
                  <option value="male">ÎÇ®ÏÑ±</option>
                  <option value="female">Ïó¨ÏÑ±</option>
                  <option value="other">Í∏∞ÌÉÄ</option>
                </select>
              </div>
            </div>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700 mb-1">ÎÇòÏù¥</label>
              <input type="number" name="age" value={profile.age} onChange={handleProfileChange} className="w-full px-3 py-2 border border-gray-300 rounded-md" required />
            </div>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700 mb-1">ÌïôÍµê</label>
              <input type="text" name="school" value={profile.school} onChange={handleProfileChange} className="w-full px-3 py-2 border border-gray-300 rounded-md" required />
            </div>
            <div className="mb-6">
              <label className="block text-sm font-medium text-gray-700 mb-1">ÌïôÎ≤à</label>
              <input type="text" name="studentId" value={profile.studentId} onChange={handleProfileChange} className="w-full px-3 py-2 border border-gray-300 rounded-md" required />
            </div>

            <div className="flex gap-4">
              <button
                type="button"
                onClick={() => setStep(1)} // "Ïù¥Ï†Ñ" Î≤ÑÌäº
                disabled={loading}
                className="w-1/2 bg-gray-200 text-gray-700 py-2 rounded-md hover:bg-gray-300"
              >
                Ïù¥Ï†Ñ
              </button>
              <button
                type="submit"
                disabled={loading}
                className="w-1/2 bg-[#9DB78C] text-white py-2 rounded-md hover:bg-[#8AA876] disabled:opacity-50"
              >
                {loading ? 'Í∞ÄÏûÖ Ï§ë...' : 'ÌöåÏõêÍ∞ÄÏûÖ ÏôÑÎ£å'}
              </button>
            </div>
          </form>
        )}

        <div className="text-center mt-6">
          {currentMode === 'login' ? (
            <p className="text-sm text-gray-600">
              Í≥ÑÏ†ïÏù¥ ÏóÜÏúºÏã†Í∞ÄÏöî?{' '}
              <button onClick={() => setCurrentMode('signup')} className="text-[#9DB78C] font-medium">
                ÌöåÏõêÍ∞ÄÏûÖ
              </button>
            </p>
          ) : (
            <p className="text-sm text-gray-600">
              Í≥ÑÏ†ïÏù¥ ÏûàÏúºÏã†Í∞ÄÏöî?{' '}
              <button onClick={() => { setCurrentMode('login'); setStep(1); }} className="text-[#9DB78C] font-medium">
                Î°úÍ∑∏Ïù∏
              </button>
            </p>
          )}
        </div>
      </div>
    </div>
  )
}
