  const handleVote = async (promptId: number, voteType: 'like' | 'dislike') => {
    if (!isAuthenticated) {
      setAuthModal("login");
      return;
    }
    setVoteLoading(promptId);
    setPromptsError(null);
    setRecommendedError(null);

    const token = localStorage.getItem('accessToken');
    if (!token) {
        setPromptsError("Ïù∏Ï¶ù ÌÜ†ÌÅ∞Ïù¥ ÏóÜÏäµÎãàÎã§.");
        setVoteLoading(null);
        return;
    }

    try {
        const response = await fetch(`${API_URL}/prompts/${promptId}/${voteType}`, {
            method: 'POST',
            headers: { 'Authorization': `Bearer ${token}` },
        });

        if (!response.ok) {
            throw new Error(`${voteType === 'like' ? 'Ï¢ãÏïÑÏöî' : 'Ïã´Ïñ¥Ïöî'} Ï≤òÎ¶¨Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.`);
        }

        const updatedPrompt: Prompt = await response.json();
        setPrompts(prevPrompts => 
            prevPrompts.map(p => p.id === updatedPrompt.id ? updatedPrompt : p)
        );
        setRecommendedPrompts(prevPrompts => 
            prevPrompts.map(p => p.id === updatedPrompt.id ? updatedPrompt : p)
        );

    } catch (err) {
        setPromptsError((err as Error).message);
    } finally {
        setVoteLoading(null);
    }
  };

  const [searchTerm, setSearchTerm] = useState("")
